on:absolute;margin-left:9%;margin-top:-41.5%}.pad-wrapper{display:grid;grid-template-columns:17vw 17vw 17vw 17vw;grid-template-rows:17vw 17vw 17vw 17vw;grid-gap:4.8vw}.drum-pad{display:block;text-align:center;height:100%;width:100%}#padLetter{opacity:0;visibility:hidden;display:none}.padID{font-size:10px;color:white;font-size:8px;margin-top:12px}.display{margin-top:20px}}

